<template>
  <div v-if="alert.message">
    <div v-if="alert.message == 'EmailNotValidated'">
      <div class="col-sm-12">
        <div class="alert alert-warning">
          <b>You need to activate your account.</b> If you lost the activation
          email,
          <a href="#/" v-on:click.prevent="sendActivation()">click here</a> to
          receive a new one.
        </div>
        <div class="spinner-border" v-if="sending">Sending...</div>
      </div>
    </div>
    <div v-else-if="alert.message == 'ActivationSent'">
      <div class="col-sm-12">
        <div class="alert alert-success">
          <p>An Activation Link has been sent to: {{ email }}</p>
          <p>
            If the email is not in your Inbox then please check your
            <b>SPAM FOLDER!</b>
          </p>
          <p>
            If you are still having problems please contact
            <a href="mailto:support@optumce.com">support@optumce.com</a> –
            thanks!
          </p>
        </div>
      </div>
    </div>
    <div class="row" v-else>
      <div class="col-sm-12">
        <div
          v-if="asHtml"
          :class="'alert ' + alert.type"
          v-html="alert.message"
        />
        <div v-else :class="'alert ' + alert.type">{{ alert.message }}</div>
      </div>
    </div>
  </div>
</template>
<script>
/* eslint-disable */
import { mapState, mapActions } from "vuex";
import axios from "axios";

export default {
  name: "app-message",
  props: {
    asHtml: {
      type: Boolean,
      default: false,
    },
    email: {
      type: String,
      default: "",
    },
  },
  data: () => ({
    sending: false
  }),
  computed: {
    ...mapState({
      alert: (state) => state.alert,
    }),
  },
  methods: {
    ...mapActions({
      clearAlert: "alert/clear",
    }),
    async sendActivation() {
        this.sending=true;
      await axios
        .post(process.env.API_URL + "/reverify-email", {
          email: this.email,
        })
        .then(() => {
          this.alert.message = "ActivationSent";
        })
        .catch((e) => {
          this.alert.message =
            e +
            '<p>Please contact <a href="mailto:support@optumce.com">support@optumce.com</a> for further assistance!</p>';
        })
        .then(()=>{
            this.sending=false;
        });
    },
  },
  watch: {
    $route(to, from) {
      // clear alert on location change
      this.clearAlert();
    },
  },
};
</script>
<style lang="scss" scoped>
</style>


